/* eslint-disable camelcase */
import api from '../services/api';

export interface IGitHubRateLimitsDTO {
  limit: number;
  remaining: number;
  reset: number;
}

export interface IGitHubProfileDTO {
  id: number;
  login: string;
  name: string;
  type: string;
  avatar_url: string;
  html_url: string;
  following: number;
  followers: number;
  public_repos: number;
}

export interface IGitHubContactDTO {
  id: number;
  login: string;
  type: string;
  avatar_url: string;
  html_url: string;
}

class GitHubRepository {
  public getRateLimits = async (): Promise<IGitHubRateLimitsDTO> =>
    api.get('rate_limit').then(response => response.data.resources.core);

  public getProfile = async (username: string): Promise<IGitHubProfileDTO> =>
    api
      .get(`users/${username}`)
      .then(response => response.data)
      .catch(error => {
        throw error;
      });

  public getContacts = async (
    username: string,
    resource: 'following' | 'followers',
    page: number,
  ): Promise<IGitHubContactDTO[]> =>
    api
      .get(`users/${username}/${resource}?per_page=100&page=${page}`)
      .then(response => response.data)
      .catch(error => {
        throw error;
      });

  public getNotFollowers = (
    following: IGitHubContactDTO[],
    followers: IGitHubContactDTO[],
  ): IGitHubContactDTO[] => following.filter(this.comparer(followers));

  // Helper function to find who is not following back.
  private comparer = (otherArray: IGitHubContactDTO[]) => {
    return (current: IGitHubContactDTO) => {
      return otherArray.filter(other => other.id === current.id).length === 0;
    };
  };
}

export default GitHubRepository;
